.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 9rem;
}

.gpt3__features-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__features-heading p {
    color: #FF8A71;
    font-family: var(--font-family);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    margin-top: 2rem;
}

.gpt3__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width:1050px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-heading h1 {
        font-size: 24px;
        font-weight: 800;
        line-height: 35px;
    }

    .gpt3__features-heading p {
        font-size: 12px;
        line-height: 20px;
        margin-top: 1rem;
    }
}

@media screen and (max-width:550px) {
    .gpt3__features-heading{
        width: 100%;
    }
    .gpt3__features-heading h1 {
        width: 100%;
        font-size: 18px;
        font-weight: 800;
        line-height: 25px;
    }
    
}